import { Injectable, Optional } from '@angular/core';
import { DataColumn, ColumnFormatter, EnumFormatOptions, ColumnFormatService } from '@farris/ui-common/column';

@Injectable({
    providedIn: 'root'
})
export class CommonUtils {
    private cfs: ColumnFormatService = null;
    constructor() {
        if (!this.cfs) {
            this.cfs = new ColumnFormatService(null, null);
        }
    }

    /**
     * è·åå¯¹è±¡ä¸­æå®å­æ®µçå¼ã field: å¯ä»¥ä¸ºå¸¦æå±çº§ç»æçè·¯å¾ï¼å¦ï¼ user.firstName | name ç­
     * data: è·åå­æ®µçæ°æ®æºï¼ä¸è¬ä¸ºJSONå¯¹è±¡
     * safe: ä¸ºtrue, å°htmlå­ç¬¦è¿è¡è½¬ç è¾åºï¼é»è®¤ä¸º false
     */
    getValue(field: string, data: any, safe = false) {
        if (!data) {
            return  '';
        }
        let resultVal = '';
        if (field.indexOf('.') === -1 && data.hasOwnProperty(field)) {
            resultVal = data[field];
        } else {
            resultVal = field.split('.').reduce((obj, key) => {
                if (obj) {
                    return obj[key];
                } else {
                    return null;
                }
            }, data);
        }

        if (safe) {
            return this.formatterValue(resultVal);
        } else {
            return resultVal;
        }
    }

    /**
     * æ´æ°æå®å¯¹è±¡ä¸­æä¸ªå­æ®µçå¼
     * @param obj è¢«æ´æ°å¯¹è±¡
     * @param field è¢«æ´æ°å­æ®µ
     * @param val æ°å¼
     * @param nest æ¯å¦ä¸ºåµå¥ï¼é»è®¤ä¸º true
     */
    setValue(obj: { [key: string]: any }, field: string, val: any, nest = true) {
        if (field) {
            if (field.indexOf('.') > -1 && nest) {
                let lastObj = null;
                const _fields = field.split('.');
                _fields.reduce( (c, p) => {
                    lastObj = c;
                    return c[p];
                }, obj);

                if (lastObj) {
                    lastObj[_fields.pop()] = val;
                }
            } else {
                obj[field] = val;
            }
        }
    }

    getEnumItem(value: any, enumData: any[], valueField) {
        const item = enumData.find(n => n[valueField] == value);
        return item;
    }

    private _getEnumTitleFromColumn(value: any, col: any) {
        const _col = col as DataColumn;
        const formatter = _col.formatter as ColumnFormatter;
        if (formatter && typeof formatter === 'object') {
            if (formatter.type === 'enum' && formatter.options) {
                return this.getEnumTitleFromColumnOptions(value, formatter.options);
            }
        }
        return value;
    }

    getEnumTitle(col: DataColumn, data: any ) {
        const val = this.getValue(col.field, data);
        return this._getEnumTitleFromColumn(val, col);
    }

    getEnumTitleFromColumnOptions(value: any, opts: any) {
        const { data, valueField, textField } = opts;
        const item = this.getEnumItem(value, data, valueField);
        if (item) {
            return item[textField];
        } else {
            return value;
        }
    }

    escapeHtml(str) {
        if (str === null || str === undefined) {
            return  '';
        }
        return str
        .replace(/&/g, '&amp;')
        .replace(/</g, '&lt;')
        .replace(/>/g, '&gt;')
        .replace(/\"/g, '&quot;')
        .replace(/\'/g, '&#39;')
        .replace(/\//g, '&#x2F;');
    }

    unescapeHtml(str) {
        if (str === null || str === undefined) {
            return  '';
        }
        return str
        .replace(/&amp;/g, '&')
        .replace(/&lt;/g, '<')
        .replace(/&gt;/g, '>')
        .replace(/&quot;/g, '"')
        .replace(/&#39;/g, '\'')
        .replace(/&#x2F;/g, '/');
    }


    private formatterValue(val: any) {
        if (val === null || val === undefined || val === '') {
            return '';
        }

        if (typeof val === 'string') {
            return this.escapeHtml(val);
        }

        return val;
    }

    /** è·åå­ç¬¦ä¸²å¨é¡µé¢ä¸­ççå®å®½åº¦ */
    getTextWidth(txt: string, font) {
        // const frag = document.createDocumentFragment();
        const canvas = document.createElement('canvas');
        const context = canvas.getContext('2d');
        context.font = font;
        const metrics = context.measureText(txt);
        return Math.round(metrics.width);
    }

    removeStyleSheet(id: string) {
        const styleElement = document.querySelector('#' + id);
        if (styleElement) {
            styleElement.remove();
        }
    }

    createStyleSheet(styleSheetId: string): HTMLStyleElement {
        const htmlHead = document.querySelector('head');

        const styleEleId = styleSheetId;
        let styleElement = null;
        if (document.querySelector('#' + styleEleId)) {
            styleElement = document.querySelector('#' + styleEleId);
            styleElement.innerText = '';
        } else {
            styleElement = document.createElement('style');
            styleElement.id = styleEleId;
            htmlHead.appendChild(styleElement);
        }

        return styleElement;
    }

    getStyleSheet(styleSheetId) {
        const styleSheets = document.styleSheets as any;
        let styleSheet = null;
        for (const stylesheet of styleSheets) {
            if ((stylesheet.ownerNode || stylesheet['owningElement']).id === styleSheetId) {
                styleSheet = stylesheet;
                break;
            }
        }

        return styleSheet;
    }

    appendCssRules(rules: string[], styleSheet) {
        for (const rule of rules) {
            const ruleName = rule.slice(0, rule.indexOf('{'));
            const removedCssRule = this.removeCssRule(ruleName, styleSheet);
            if (styleSheet.addRule) {
                let cssText = '';
                if (removedCssRule) {
                    cssText += removedCssRule.cssText.slice(removedCssRule.cssText.indexOf('{') + 1, removedCssRule.cssText.indexOf('}'));
                }
                cssText += rule.slice(rule.indexOf('{') + 1, rule.indexOf('}')).replace(/"/g, '').replace(/,/g, ';') + ';';
                styleSheet.addRule(ruleName, cssText, 0);
            } else {
                styleSheet.insertRule(rule, 0);
            }
        }
    }

    getCssRule(ruleName: string, styleSheet) {
        const cssRules = (styleSheet.cssRules || styleSheet.rules) as any;
        let r = null;
        for (const rule of cssRules) {
            if (rule.selectorText == ruleName) {
                r = rule;
            }
        }

        return r;
    }

    removeCssRule(ruleName: string, styleSheet ) {
        const cssRules = (styleSheet.cssRules || styleSheet.rules) as any;
        let ii = 0;
        let cssRule: any = false;
        do {
            cssRule = cssRules[ii];
            if (cssRule) {
                if (cssRule.selectorText.toLowerCase() == ruleName.toLowerCase()) {
                    if (styleSheet.cssRules) {
                        styleSheet.deleteRule(ii);
                    } else {
                        styleSheet.removeRule(ii);
                    }

                    return cssRule;
                }
            }
            ii++;
        } while (cssRule);
    }

    isNullOrUndefined(val: any) {
        return val === null || val === undefined;
    }

    isIE() {
        const uA = window.navigator.userAgent;
        return /msie\s|trident\/|edge\//i.test(uA) && !!('uniqueID' in document || 'documentMode' in document || ('ActiveXObject' in window) || 'MSInputMethodContext' in window);
    }

    buildSortString(sorts: any[]): string {
        return this.cfs.buildSortString(sorts);
    }

    buildSqlWhere(conditions: any[]): string {
        return this.cfs.buildSqlWhere(conditions);
    }
}
